import {
  Input,
  MaskedTextBox,
  NumericTextBox,
} from "@progress/kendo-react-inputs";
import { DatePicker } from "@progress/kendo-react-dateinputs";
import { Controller, useForm } from "react-hook-form";
import { KendoFormType } from "../../types/UserFormType";
import { DropDownList } from "@progress/kendo-react-dropdowns";

const KendoForm = () => {
  const roles = ["user", "admin", "manager"];

  const {
    control,
    handleSubmit,
    formState: { errors },
  } = useForm<KendoFormType>();

  const onSubmit = (data: KendoFormType) => {
    console.log("FORM RESULT:", data);
  };

  return (
    <form className="kendoFormContainer" onSubmit={handleSubmit(onSubmit)}>
      {/* Full name */}
      <Controller
        name="fullName"
        control={control}
        defaultValue=""
        rules={{ required: "Введите имя" }}
        render={({ field, fieldState }) => (
          <>
            <Input
              className="kendoInput"
              {...field}
              value={field.value}
              onChange={(e) => field.onChange(e.value)}
              placeholder="ФИО"
            />
            {fieldState.error && (
              <p className="error">{fieldState.error.message}</p>
            )}
          </>
        )}
      />

      {/* Phone */}
      <Controller
        name="phone"
        control={control}
        defaultValue=""
        rules={{
          required: "Введите телефон",
          minLength: { value: 17, message: "Введите полностью" },
        }}
        render={({ field, fieldState }) => (
          <>
            <MaskedTextBox
              {...field}
              mask="+0 (000) 000-0000"
              value={field.value}
              onChange={(e) => field.onChange(e.value)}
              placeholder="+7 (___) ___-____"
            />
            {fieldState.error && (
              <p className="error">{fieldState.error.message}</p>
            )}
          </>
        )}
      />

      {/* Age */}
      <Controller
        name="age"
        control={control}
        defaultValue={0}
        rules={{
          required: "Укажите возраст",
          min: { value: 18, message: "Минимум 18 лет" },
        }}
        render={({ field, fieldState }) => (
          <>
            <NumericTextBox
              {...field}
              value={field.value}
              onChange={(e) => field.onChange(e.value)}
              placeholder="Возраст"
            />
            {fieldState.error && (
              <p className="error">{fieldState.error.message}</p>
            )}
          </>
        )}
      />

      {/* Date */}
      <Controller
        name="startDate"
        control={control}
        defaultValue={undefined}
        rules={{ required: "Выберите дату" }}
        render={({ field, fieldState }) => (
          <>
            <DatePicker
              {...field}
              value={field.value}
              onChange={(e) => field.onChange(e.value)}
              format="dd.MM.yyyy"
              placeholder="Дата начала"
            />
            {fieldState.error && (
              <p className="error">{fieldState.error.message}</p>
            )}
          </>
        )}
      />
      {/* DropDownList */}
      <Controller
        name="rule"
        control={control}
        defaultValue=""
        rules={{ required: "Выберите роль" }}
        render={({ field, fieldState }) => (
          <>
            <DropDownList
              data={roles}
              value={field.value}
              onChange={(e) => field.onChange(e.value)}
            />
          </>
        )}
      />
      <button type="submit">Отправить</button>
    </form>
  );
};

export default KendoForm;
